.warehouse {
    .card {
      padding: 1rem;
      margin-top: 1rem;
    }
    .review {
      border-top: 1px solid #ccc;
    }
    .details {
      padding-top: 2rem;
      display: flex;
  
      .img {
        width: 45%;
        border: 1px solid #ccc;
        border-radius: 3px;
        img {
          width: 100%;
        }
      }
  
      .content {
        width: 55%;
        padding: 0 5px;
        & > * {
          margin-bottom: 1rem;
        }
        .price {
          color: orangered;
          font-weight: 500;
        }
  
        .count {
          display: flex;
          align-items: center;
          & > * {
            margin-right: 1rem;
          }
        }
      }
    }
  }
  
  @media screen and (max-width: 700px) {
    .warehouse {
      .details {
        flex-direction: column;
  
        .img {
          width: 100%;
        }
  
        .content {
          width: 100%;
        }
      }
    }
  }